import React from 'react';
import {Slider, Slide, Caption} from 'react-materialize';

function Header(){
return (
<Slider

  className='z-depth-1'
  fullscreen={false}
  options={{
    duration: 500,
    height: 400,
    indicators: true,
    interval: 6000
  }}
>
  <Slide image={<img alt="Feeling Hungry App" src="data:image/jpeg;base64,"/>}>
    <Caption placement="center">
      <h3 href='https://mbelfrom.github.io/group-project-mag/'>
        Feeling Hungry?
      </h3>
      <h5 className="light grey-text text-lighten-3">
      Have you ever been stuck not knowing what to eat for dinner? Our web app will generate a recipe for you. Just choose from chicken, beef, or vegan options and a recipe will generate. Too tired to cook? Our web app will also suggest the nearest restaurant to you. You also have the option to add and remove your favorite recipes.
      </h5>
    </Caption>
  </Slide>
  <Slide image={<img alt="Coding Jobs App" src="data:image/png;base64,"/>}>
    <Caption placement="left">
      <h3 href='https://coding-jobs-345234.herokuapp.com'>
        2 Chit 2 Chat
      </h3>
      <h5 className="light grey-text text-lighten-3">
      Use this app to make chat rooms with your friends!
      </h5>
    </Caption>
  </Slide>
  <Slide image={<img alt="Project 3" src="data:image/jpeg;base64,"/>}>
    <Caption placement="right">
      <h3>
        Get Outta Town
      </h3>
      <h5 className="light grey-text text-lighten-3">
        Create and compare travel plans with this app!
      </h5>
    </Caption>
  </Slide>
</Slider>
)
}

export default Header;