import React from 'react';
import {MediaBox} from 'react-materialize';


function Profile(){

return(
  
<MediaBox
  id="MediaBox_9"
  options={{
    inDuration: 275,
    onCloseEnd: null,
    onCloseStart: null,
    onOpenEnd: null,
    onOpenStart: null,
    outDuration: 200
  }}
>
  <img
    alt="Profile Picture"
    src="data:image/png;base64,"
    width="400"
  />
</MediaBox>
)
}
export default Profile;